}

.upgrade-li {
  /* margin-right: 9px; */
  color: lightgrey;
  text-transform: capitalize;
  margin-top: 3px;
}

.upgrade-li-bought {
  color: grey;
  border: none;
  text-transform: capitalize;
  margin-top: 3px;
  /* color: rgb(75, 75, 75); */
}

.upgrade-li-bought:hover, .upgrade-li:hover {
  color: #0096ff;
  cursor: pointer;
}



/* BUY BUILDING BUTTON/OVERLAY on building component */
.buy-building {
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  background: white;
  font-size: 14px;
  padding: 5px;

  border-radius: 4px;
  box-shadow: 1px 1px 2px -1px #aeaeaeb8;
}

.buy-building:hover {
  cursor: pointer !important;
  /* background: lightblue; */
  /* background: #0096ff; */
  color: #0096ff;
}



