/* SIDEBAR */
.sidebar-wrap {
  /* background: lightpink; */
  width: 230px;
  margin-right: 20px;
  border: 2px solid lightgrey;
  box-sizing: border-box;
}

.sidebar-nogirl-selected {
  padding-left: 5px;
  padding-top: 5px;
  font-size: 13px;
}

/* TOP SIDEBAR ****************************************************************/
.sidebar-name {
  padding-top: 5px;
  padding-left: 5px;
}

.sidebar-name, .sidebar-rank, .sidebar-location, .price-cost {
  font-size: 14px;
}

.sidebar-rank, .sidebar-location {
  /* cursor: help; */
  padding-left: 5px;
}

.rank:hover, 
.location:hover, 
.price:hover, .cost:hover {
  color: #00a7ee;
}

.rank-num, .location-val, .price-cost-val {
  color: grey;
  text-transform: capitalize;
}

.price-cost {
  display: flex;
  justify-content: space-between;
  padding: 0 5px;
}

.price-cost-val {

}

.set-price-cost {
  display: flex;
  /* padding-left: 5px; */
  padding: 5px;
}

.set-price-cost button {
  margin-right: 5px;
}

.set-price-cost button:hover {
  cursor: pointer;
  color: #00a7ee;
}



.sidebar-pic {
  width: 100%;
}


/* BOTTOM SIDEBAR *************************************************************/
.sidebar-bottom {
  display: flex;
  justify-content: space-between;
  height: 310px;
}

.sidebar-bottom-left {
  width: 60%;
  /* background: lightskyblue */
}

.sidebar-bottom-right {
  width: 40%;
  /* background: lightgreen; */
}


/* STATS **********************************************************************/
.stats-wrap {
  height: 75%;
  padding-left: 5px;
  /* background: lightgrey; */
}

.stats-wrap p {
  /* font-size: 16px; */
  font-size: 14px;
}

.stats-title:hover {
  color: #00a7ee;
}

.stats {
}

#sidebar-stats-table {
  font-size: 14px;
  width: 100%;
  text-transform: capitalize;
}

.stats-num {
  color: darkgrey;
}

.stats-name {
  color:grey;
}

.stats-name:hover {
  color: #00a7ee;
}

/* .stats-name-exp, .stats-num-exp { */
.stats-name-exp {
  /* color: #333030; */
  color: grey;
  font-size: 14px;
  display: block;
  padding-left: 2px;
}

.stats-name-exp p {
  font-size: 14px;
  display: inline;
  margin-left: 43px;
}

.stats-name-exp:hover {
  color: #00a7ee;
}

/* #sidebar-stats-table tr {
  background: #00a7ee;
} */

#sidebar-stats-table tr:hover {
  /* background: lightblue; */
  background: #00a7ee54;
  /* color: #00a7ee !important; */
}

.stats-button:hover {
  cursor: pointer;
  color: #00a7ee;
}

.hidden-button {
  visibility: hidden;
}

/* SERVICES *******************************************************************/
.services-wrap {
  height: 25%;
  padding-top: 5px;
  padding-left: 5px;
  /* background: lightgrey; */
}

.choose-services-button:hover {
  cursor: pointer;
  color:#00a7ee;
}

.services-li {
  font-size: 14px;
  text-transform: capitalize;
  color:grey;
}

.services-li:hover {
  color: #00a7ee;
}

.choose-services-button {
  /* font-size: 16px; */
  font-size: 14px;
  border-radius: 4px;
  box-shadow: 1px 1px 2px -1px #aeaeaeb8;
}

/* ABILITIES ******************************************************************/
.abilities-wrap {
  height: 35%;
  padding-left: 5px;
  /* background: lightskyblue; */
}

.abilities-title {
  /* font-size: 16px; */
  font-size: 14px;
}

.abilities-title:hover {
  color: #00a7ee;
}

/* .abilities-wrap li { */
.abilities-wrap button {
  /* color: lightgrey; */
  /* font-size: 14px; */
  font-size: 11px;
  text-transform: capitalize;
  padding-bottom: 4px;
}

.learned-ability {
  color: grey;
}

.learned-ability:hover {
  color: #00a7ee;
}

.potential-ability {
  color: lightgrey;
}

.potential-ability:hover {
  cursor: pointer;
  color: #00a7ee;
}


/* INVENTORY **********************************************************************/
.inventory-wrap {
  height: 60%;
  padding-left: 5px;
  /* background: darkcyan; */
}

.inventory-li {
  text-transform: capitalize;
  color: lightgrey;
  padding-bottom: 4px;
  /* font-size: 14px; */

  font-size: 11px;  
}

.inventory-li-owned {
  color:grey;
  text-transform: capitalize;
  padding-bottom: 4px;
  /* font-size: 14px; */

  font-size: 11px;
}

.inventory-li-owned:hover {
  color: #00a7ee;
}

.inventory-li:hover {
  cursor: pointer;
  color:#00a7ee;
}


/* For Choose Services Modal Checkboxes */
input:hover {
  cursor: pointer;
}